import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import async from 'async'
import { getHomeState } from '../state/Home';
import getDashboardDataFromAddress from '../utils/dapp'
import useAuth from '../hooks/useAuth';
import Header from '../components/Header'
import styled from 'styled-components'
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup'
import ToggleButton from '@mui/material/ToggleButton'
import Button from '@mui/material/Button'
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Slider from '@mui/material/Slider';
import DragAndDrop from '../components/DragAndDrop';
import Footer from '../components/Footer'

const Container = styled.div`
	width: 80%;
	min-width: 600px;
	margin: 0 auto;
	display: grid;
`
const ContentContainer = styled.div`
	background-color: white;
	border-radius: 4px;
	box-shadow: 0 1px 2px #aaa;
	min-height: 300px;
	padding: 1rem;
`
const FlexContent = styled.div`
	display: flex;
`
const Text = styled.p`
	color: #868e96;
	margin: 0;
`

const TextArea = styled.textarea`
	resize: none;
	overflow-y: scroll;
`
const PrettoSlider = styled(Slider)({
    color: '#52af77',
    height: 8,
    '& .MuiSlider-track': {
	//backgroundColor: '#52af77',
      border: 'none',
    },
    '& .MuiSlider-thumb': {
      height: 12,
      width: 12,
 //     backgroundColor: '#52af77',
      border: '2px solid currentColor',
      '&:focus, &:hover, &.Mui-active, &.Mui-focusVisible': {
        boxShadow: 'inherit',
      },
      '&:before': {
        display: 'none',
      },
    },
    '& .MuiSlider-valueLabel': {
      lineHeight: 1.2,
      fontSize: 10,
      background: 'unset',
      padding: 0,
      width: 18,
      height: 18,
      borderRadius: '50% 50% 50% 0',
      backgroundColor: '#1976d2',
      transformOrigin: 'bottom left',
      transform: 'translate(50%, -100%) rotate(-45deg) scale(0)',
      '&:before': { display: 'none' },
      '&.MuiSlider-valueLabelOpen': {
        transform: 'translate(50%, -100%) rotate(-45deg) scale(1)',
      },
      '& > *': {
        transform: 'rotate(45deg)',
      },
    },
});

const FileDropZone = styled.div`
	position: relative;
	min-height: 180px;
	max-height: 490px;
	overflow-y: hidden;
	width: inherit;
	border-radius: 6px;
	border: 1px dashed #ccc;
	background-color: #fafafa;
	padding: 20px;

`
const DropText = styled.div`
	text-align: center;
    font-size: 32px;
    color: #cccccc;
    pointer-events: none;
    margin-top: 10px;
`

const styles = {
	label: {
		lineHeight: "40px",
		marginRight: "5px"
	}
}

const marks = [
	{
	  value: 0,
	  label: '0',
	},
	{
	  value: 25,
	  label: '25',
	},
	{
	  value: 50,
	  label: '50',
	},
	{
		value: 75,
		label: '75',
	},
	{
	  value: 100,
	  label: '100',
	},
];


const Home = () => {
	const [type, setType] = useState('BNB')
	const [files, setFiles] = useState([])

	const handleType = (event, newType) => {
		setType(newType);
	};

	const handleDrop = (newFiles) => {
		console.log("HandleDrop");
		console.log(newFiles)
		let fileList = files;
		for(let i = 0;i<newFiles.length;i++){
			if(!newFiles[i].name) return
			fileList.push(newFiles[i])
		}
		setFiles(fileList)
	}

	return (
		<Container>
			<Header />
			<ContentContainer>
				<div><h1>Upload, share and sale for BNB</h1></div>
				<FlexContent className="row">
					<div className='col-lg-6 col-md-6'>
						<Text>Upload any files (max file size 50MB):</Text>
						<DragAndDrop handleDrop={handleDrop}>
							<FileDropZone>
								<Button variant="contained">Upload a file</Button>
								{files.length ? (files.map((file) =>
									<div key={file.name}>{file}</div>
								)):(
									<DropText>
										Drop Here
									</DropText>
								)}
							</FileDropZone>
						</DragAndDrop>
					</div>
					<div className='col-lg-6 col-md-6'>
						<Text>or text for sale:</Text>
						<TextArea className="form-control" name="symbols" rows="7" data-toggle="tooltip" data-placement="top"
						title=""
						data-original-title="You can sale text only without any files. Or sale files+text. As you wish. Max text lenght: 64kb."></TextArea>
					</div>
					{/*<div className="col-lg-12 text-center mt-2">
			            <span title="Prohibited transmit any viruses, worms, defects, Trojan horses or other items of a contaminating or destructive nature, except when transmitting to a knowing recipient while conducting security research. PLEASE archive it with passwords, otherwise antivirus will delete it.">
							<img src="https://satoshidisk.com/themes/satoshidisc/assets/images/bug.svg" height="20" alt="Antivirus scan" /> Antivirus scan.
						</span>
						<img src="https://satoshidisk.com/themes/satoshidisc/assets/images/warning.svg" height="20" alt="Restricted content" /> Restricted content: obscene, pornographic, abusive, harassing, threatening. It will be deleted.
					</div>*/}
				</FlexContent>
			</ContentContainer>
			<ContentContainer style={{marginTop: '1rem'}}>
				<FlexContent className='row'>
					<div className='col-lg-6 col-md-6'>
						<Text>Open info about your fils (optional): </Text>
						<TextArea className="form-control" title="" name="openinfo" rows="12" data-toggle="tooltip" data-placement="top"
						placeholder={"What you sale:"+"\n\n"+"Short description:" + "\n\nContact info:"+"\n\nemail, phone, social media, jabber, etc."}
						data-original-title="Buyers will pay faster If have more seller's info and reputation."></TextArea>
					</div>
					<div className='col-lg-6 col-md-6'>
						<div className="form-group">
							<Text>Your Wallet Address<span className="text-danger">*</span></Text>
							<input type="text" name="bitcoin-address" className="form-control" placeholder="" required="" data-toggle="tooltip" data-placement="top" title="" data-original-title="Required field. Please make sure you enter and double-checked the right yours Bitcoin address to receive payment. (neither bitcoin cash nor another same format address)" readOnly />
						</div>
						<div className="row">
							<p className="col-12 text-muted m-sm-0">Price<span className="text-danger">*</span>:</p>
							<div className="col">
							<div className="input-group form-group">
								<div style={styles.label}>BTC</div>
								<input name="price" type="price" className="form-control" value="0.01" maxLength="17" data-toggle="tooltip" data-placement="top" title="" data-original-title="Required field. You will receive Bitcoins only."/>
							</div>
							</div>
							<div className="col-lg-5 col-md-5 col-sm-5 col-xs-5">	
								<div className="input-group form-group">
									<div style={styles.label}>$</div>
									<input name="usd" type="price" className="form-control" value="140" maxLength="10" data-toggle="tooltip" data-placement="top" title="" data-original-title="Amount in USD will be calculated into BTC and store. We accept Bitcoins and forward to yours wallet."/>
								</div>
							</div>
							<div className="col-12 text-center">
								<ToggleButtonGroup
									value={type}
									onChange={handleType}
									exclusive
									fullWidth={true}
									size="small"
									color={'secondary'}
								>
									<ToggleButton variant="contained" color="primary" value="BNB">Fix Price in BNB</ToggleButton>
									<ToggleButton value="USD">Fix Price in USD</ToggleButton>
								</ToggleButtonGroup>
							</div>
							<div className="col-12">
								<Text className="mb-4 mt-3">Count of Sales (Zero is an unlimited Sales):</Text>
								<Box sx={{width: '90%'}} className="text-center">
									<PrettoSlider
										aria-label="NFT Count"
										defaultValue={1}
										step={1}
										valueLabelDisplay="on"
										marks={marks}
									/>
								</Box>
							</div>
						</div>
					</div>
					<div className="col-lg-12 text-center mt-4">
						<input name="filenames" type="text" hidden={true} />
						<input name="filedirnames" type="text" hidden={true} />
						<Button variant="contained">Upload and Share</Button>
					</div>
				</FlexContent>
			</ContentContainer>
			<Footer/>
		</Container>
	)
}

export default Home;